.content img {
  width: 100%;
  height: 200px;
  border: 10px solid #ffffff;
  box-sizing: border-box;
}

.img img{
  width: 400px;
  height: 400px;
  border: 10px solid #ffffff;
  box-sizing: border-box;
}

.descr{
  display: flex;
}

.userInfo{
  padding: 20px;
}

.name h1{
  margin: 0;
}

.name h3{
  margin: 0;
  color: rgb(141, 141, 141);
}

.job{
  margin: 10px 0px;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
}


.job img{
  width: 40px;
  height: 40px;
  border: none;
}

.mark{
  display: flex;
  align-items: center;
}

.job span{
  display: block;
  color: rgb(136, 133, 133);
  margin-bottom: 20px;
}

.contacts p{
  margin: 0;
}